import React, { FC } from 'react';
import * as Yup from 'yup';
import { Button, Col, Form, Row } from 'react-bootstrap';

import { TUser } from '../../helpers/types';
import './UserDataForm.scss';
import { Formik } from 'formik';

const UserDataSchema = Yup.object().shape({
  fullName: Yup.string()
    .min(3, '  \'!')
    .max(150, '  \'!')
    .required(' \''),
  address: Yup.string()
    .min(3, '  ')
    .max(250, '  ')
    .required(' '),
  newPostNumber: Yup.number()
    .min(1, '   ')
    .max(5, '   ')
    .required('  '),
  terms: Yup.bool().required().oneOf([true], '  '),
});

export interface IUserDataForm {
  userData: TUser;
  onSubmitForm: (newUserData: TUser) => void;
}
export const UserDataForm: FC<IUserDataForm> = ({
  userData,
  onSubmitForm,
}) => { 
  const { fullName, address, newPostNumber } = userData;

  const handleSubmitForm = (userData: TUser) => {
    const { fullName, address, newPostNumber } = userData;

    onSubmitForm({ fullName, address, newPostNumber })
  }

  return (
    <Formik
      validationSchema={UserDataSchema}
      onSubmit={handleSubmitForm}
      initialValues={{
        fullName: fullName,
        address: address,
        newPostNumber: newPostNumber || '',
        terms: false,
      }}
    >
      {({
        handleSubmit,
        handleChange,
        values,
        touched,
        errors,
      }) => (
        <Form onSubmit={handleSubmit}>
          <Row>
            <Form.Group as={Col} controlId="fullName" className="pt-3">
              <Form.Label>, '    </Form.Label>
              <Form.Control
                type="text"
                name="fullName"
                value={values.fullName}
                onChange={handleChange}
                isValid={touched.fullName && !errors.fullName}
                isInvalid={touched.fullName && !!errors.fullName}
              />
              <Form.Control.Feedback type="invalid">
                {errors.fullName}
              </Form.Control.Feedback>
            </Form.Group>
          </Row>
          <Row>
            <Form.Group as={Col} controlId="address" className="pt-3">
              <Form.Label> ( , , )</Form.Label>
              <Form.Control
                type="text"
                name="address"
                value={values.address}
                onChange={handleChange}
                isValid={touched.address && !errors.address}
                isInvalid={touched.address && !!errors.address}
              />
              <Form.Control.Feedback type="invalid">
                {errors.address}
              </Form.Control.Feedback>
            </Form.Group>
          </Row>
          <Row>
            <Form.Group as={Col} controlId="newPostNumber" className="pt-3">
              <Form.Label>  " "</Form.Label>
              <Form.Control
                type="text"
                name="newPostNumber"
                value={values.newPostNumber}
                onChange={handleChange}
                isValid={touched.newPostNumber && !errors.newPostNumber}
                isInvalid={touched.newPostNumber && !!errors.newPostNumber}
              />
            </Form.Group>
          </Row>
          <Row>
            <Form.Group as={Col} className="pt-3">
              <Form.Check
                name="terms"
                label="  "
                onChange={handleChange}
                isInvalid={!!errors.terms}
                feedback={errors.terms}
                feedbackType="invalid"
              />
            </Form.Group>
          </Row>
          <Row>
            <Col className="text-end pt-3">
              <Button type="submit"> </Button>
            </Col>
          </Row>
        </Form>
      )}
    </Formik>
  );
};
